import React, { Component } from 'react';
import Timepicker from '../../src/index';

class Demo1 extends Component {

    onChange(time){
        console.log(time);
    }
    getPopupContainer() {
        return this.d || document.getElementById('d');
    }
    render() {
        return (
            <div id={"d"}>
                <Timepicker getPopupContainer={this.getPopupContainer} placeholder="" onChange={this.onChange.bind(this)} />
            </div>
        )
    }
}


export default Demo1;


